.content{
    width: 282px;
}
.content .imgbox{
    width: 100%;
    height: 210px;
}
.content .imgbox img{
    width: 100%;
    height: 100%;
}
.content .txt{
    width: 100%;
    margin-top: 10px;
}
.content .txt p{
    font-size: 12px;
    color: #7A72A4;
    font-weight: 500;
}
.content .txt h4{
    font-weight: 500;
    margin: 10px 0 24px 0;
}
.content .txt h2{
    font-weight: 600;
}
.content .txt h2 span{
    font-size: 18px;
    font-weight: 500;
}